.modal {
    height: auto;
    min-height: 20vh;
    width: 20vw;
    background-color: white;
    position: fixed;
    top: 20%;
    left: 50%;
    border-radius: 1rem;
}

.overlay {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.2;
}

.close {
    display: flex;
    position: absolute;
    top: 10px;
    right: 10px;
    padding: .5em;
    text-decoration: none;
    font-size: 1em;
    box-sizing: border-box;
    background-color: #213363;
    cursor: pointer;
    clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
}